import {
  Button,
  Checkbox,
  FormControlLabel,
  FormGroup,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import swal from "sweetalert2";
import React from "react";
import { Helmet } from "react-helmet";
import getAPIBaseUrl from "./getAPIBaseUrl";
import { DateTimePicker } from "@mui/x-date-pickers/DateTimePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterMoment } from "@mui/x-date-pickers/AdapterMoment";

export default function CreateTest() {
  const formik = useFormik({
    initialValues: {
      title: "",
      code: "",
      isTemporarilyTest: false,
      temporarilyDate: new Date().valueOf(),
    },
    validateOnChange: true,
    onSubmit: async (values) => {
      const req = await fetch(`${getAPIBaseUrl()}/api/create-test`, {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          title: values.title,
          questions: JSON.parse(values.code),
          isTemporarilyTest: values.isTemporarilyTest,
          temporarilyDate: values.temporarilyDate,
        }),
      });

      const { err, id } = await req.json();
      if (err) {
        return swal.fire({
          icon: "error",
          text: err,
          title: "Произошла ошибка ⛔️",
        });
      }

      swal.fire({
        icon: "success",
        html: `<a href="${document.location.origin}/#/${id}">${document.location.origin}/#/${id}</a>`,
        title: "Успешно ✅",
      });
    },
    validate: (values) => {
      const errors = {};

      const { code, title } = values;
      try {
        JSON.parse(code);
      } catch (e) {
        errors["code"] = "В коде допущена ошибка!";
      }

      if (!title) {
        errors.title = "Данное поле обязательно!";
      }

      return errors;
    },
  });
  return (
    <div className="center">
      <Typography style={{ marginTop: 30, marginBottom: 30 }} variant="h4">
        Создание теста
      </Typography>
      <form className="container" onSubmit={formik.handleSubmit}>
        <Helmet>
          <title>Создание теста</title>
        </Helmet>
        <TextField
          style={{ marginTop: 30 }}
          name="title"
          fullWidth
          variant="filled"
          label="Введите заголовок теста"
          onChange={formik.handleChange}
          value={formik.values.title}
          helperText={formik.errors.title}
          error={!!formik.errors.title}
        />
        <FormGroup style={{ marginTop: 10, marginBottom: 10 }}>
          <FormControlLabel
            control={
              <Checkbox
                name="isTemporarilyTest"
                onChange={formik.handleChange}
                value={formik.values.isTemporarilyTest}
              />
            }
            label="Сделать тест временным"
          />
        </FormGroup>

        <FormGroup style={{ marginTop: 10, marginBottom: 10 }}>
          <LocalizationProvider dateAdapter={AdapterMoment}>
            <DateTimePicker
              label="Тест будет активен до"
              name="temporarilyDate"
              disabled={!formik.values.isTemporarilyTest}
              value={new Date(Number(formik.values.temporarilyDate))}
              onChange={(e) => {
                formik.handleChange("temporarilyDate")(
                  String(e.toDate().valueOf())
                );
              }}
              renderInput={(params) => (
                <TextField variant="filled" {...params} />
              )}
            />
          </LocalizationProvider>
        </FormGroup>

        <TextField
          fullWidth
          variant="filled"
          label="Введите вопросы в формате JSON"
          multiline
          helperText={formik.errors.code}
          error={!!formik.errors.code}
          rows={19}
          name="code"
          onChange={formik.handleChange}
          value={formik.values.code}
        />
        <Button
          fullWidth
          type="submit"
          variant="contained"
          style={{ marginTop: 20 }}
        >
          Создать
        </Button>
      </form>
    </div>
  );
}
