import {
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect } from "react";
import { useFormik } from "formik";
import { useParams, useNavigate } from "react-router-dom";
import Loader from "./Loader";
import Helmet from "react-helmet";
import swal from "sweetalert2";
import getAPIBaseUrl from "./getAPIBaseUrl";
import { useTimer } from "react-timer-hook";

const Test = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [data, setData] = React.useState({
    title: "",
    questions: [],
    id: "",
  });

  const {
    seconds,
    minutes,
    start: startTimer,
    pause,
    restart,
  } = useTimer({
    expiryTimestamp: new Date().setMinutes(new Date().getMinutes() + 10),
    onExpire: async () => {
      const { values } = formik;
      const copyValues = JSON.parse(
        JSON.stringify({ ...initialValues, ...values })
      );
      delete copyValues["nickname"];
      formik.resetForm();

      if (values.nickname) {
        await fetch(`${getAPIBaseUrl()}/api/send-results-test`, {
          method: "post",
          body: JSON.stringify({
            testId: data.id,
            nickname: values.nickname,
            answers: copyValues,
          }),
          headers: {
            "Content-Type": "application/json",
          },
        });
      }

      if (values.nickname) {
        navigate("/run-out-time?__rewkrew__=1");
      } else {
        navigate("/run-out-time");
      }
    },
  });

  useEffect(() => {
    const asyncFunc = async () => {
      const req = await fetch(`${getAPIBaseUrl()}/api/tests/${id}`, {
        method: "get",
      });
      const { data, err } = await req.json();
      if (err) {
        return navigate("/");
      }

      setData(data);
      startTimer();
    };
    if (!data.title) {
      asyncFunc();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data.title]);

  const initialValues = {
    nickname: "",
  };
  for (const question of data.questions) {
    initialValues[question.id] = "";
  }

  const formik = useFormik({
    initialValues,
    onSubmit: async (values) => {
      const nickname = String(values.nickname);
      if (!nickname) {
        const date = new Date();
        date.setMinutes(date.getMinutes() + 6);
        return restart(date);
      }
      const copyValues = JSON.parse(
        JSON.stringify({ ...initialValues, ...values })
      );
      delete copyValues["nickname"];
      const req = await fetch(`${getAPIBaseUrl()}/api/send-results-test`, {
        method: "post",
        body: JSON.stringify({
          testId: data.id,
          nickname,
          answers: copyValues,
        }),
        headers: {
          "Content-Type": "application/json",
        },
      });

      const { err, ok } = await req.json();

      if (err || !ok) {
        return swal.fire({
          icon: "error",
          text: err || "Повторите попытку!",
          title: "Произошла ошибка ⛔️",
        });
      }

      // swal.fire({
      //   icon: "success",
      //   text: "Ваши ответы успешно были отправлены на проверку!",
      //   title: "Успешно ✅",
      // });
      pause();
      navigate("/ok");
    },
    validate(values) {
      const errors = {};

      for (const question of data.questions) {
        if (!question.required) {
          continue;
        }

        if (!values[question.id]) {
          errors[question.id] = "Данное поле обязательно!";
        }
      }

      return errors;
    },
  });

  if (!data.title) {
    return <Loader />;
  }

  return (
    <form onSubmit={formik.handleSubmit} className="center">
      <div className="timer">
        <span>
          {minutes}:{seconds}
        </span>
      </div>
      <Helmet>
        <title>{data.title}</title>
      </Helmet>
      <Typography style={{ marginTop: 30, marginBottom: 30 }} variant="h4">
        {data.title}
      </Typography>
      <div className="container" style={{ marginTop: 30, marginBottom: 30 }}>
        <TextField
          variant="filled"
          name="nickname"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          fullWidth
          required
          label="Ваш игровой никнейм"
        />
      </div>
      <div
        className="container questions"
        style={{ marginTop: 30, marginBottom: 30 }}
      >
        {data.questions.map((question) => {
          return (
            <FormControl
              style={{
                background: "#606060",
                padding: 10,
              }}
              error={formik.errors[question.id]}
              key={`question-${question.id}`}
            >
              <FormLabel>
                {question.text}
                {question.required ? "*" : ""}
              </FormLabel>
              <RadioGroup
                value={formik.values[question.id] || ""}
                onChange={formik.handleChange(question.id)}
              >
                {question.answers.map((answer) => (
                  <FormControlLabel
                    key={`answer-${answer.id}`}
                    value={answer.variant}
                    control={<Radio />}
                    label={answer.variant}
                  />
                ))}
              </RadioGroup>
            </FormControl>
          );
        })}

        <Button
          fullWidth
          type="submit"
          variant="contained"
          style={{ marginTop: 20 }}
        >
          Отправить
        </Button>
      </div>
    </form>
  );
};

export default Test;
